import { useState, createContext } from "react";
import "./App.css";
import Layout from "./Components/Layout";
import Home from "./Components/Pages/Home";
import About from "./Components/Pages/About";
import PropertyDetail from "./Components/Pages/PropertyDetail";
import Blog from "./Components/Pages/Blogs";
import Team from "./Components/Pages/OurAgents";

import { Routes, Route } from "react-router-dom";
import AgentsCard from "./Components/AgentsCard";

// import ToTop from './Components/Button/ToTop';
export const AppContext = createContext();

function HomePage() {
  return (
    <Layout>
      <Home />
    </Layout>
  );
}

function AboutUs() {
  return (
    <Layout>
      <About />
    </Layout>
  );
}

function PropertyInfo() {
  return (
    <Layout>
      <PropertyDetail />
      <style>
        {`
      .saleCard  {
        background-color: #F9F9F9;
      }
    `}
      </style>
    </Layout>
  );
}

function OurAgents() {
  return (
    <Layout>
      <Team />
    </Layout>
  );
}
function Blogs() {
  return (
    <Layout>
      <Blog />
    </Layout>
  );
}

function App() {
  const [toggle, setToggle] = useState(false);

  function handleToggle() {
    setToggle((prev) => !prev);
  }

  return (
    <AppContext.Provider value={{ toggle, setToggle, handleToggle }}>
      <Routes>
        <Route path="/" element={<HomePage />} />
        <Route path="/about" exact element={<AboutUs />} />
        <Route path="/detail" exact element={<PropertyInfo />} />
        <Route path="/blogs" element={<Blogs />} />
        <Route path="/agents" element={<OurAgents />} />
      </Routes>
    </AppContext.Provider>
  );
}

export default App;
